import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import { useContext,useState } from 'react'
import {AnalyticContext } from '../../contexts/analyticContext'

const UpdateAnalyticsModal = () => {

    const {
        analyticState:{analytic,nowAna},
        findAnalytic,
        updateAnalytic,
        showUpdateModal,
        setShowUpdateModal,
        setShowToast
    } = useContext(AnalyticContext)
/*
{
        position:'',
        total:0,
        death: 0,
        cured: 0,
        cure: 0
    }

*/

    const [newData, setNewData] = useState(nowAna)

    const {position,total,death,cured,cure} = newData

    const loadAna = pos => {
        return findAnalytic(pos)
    }

    const onChangeSelect = async event => {
        if(event.target.value !== "") {
            const data = await loadAna(event.target.value)
            setNewData(data)
        }
    }

    const onChangeDataForm = event => setNewData({ ...newData, [event.target.name]: event.target.value })

    const onSubmit = async event => {
		event.preventDefault()
        console.log(newData)
		const {success, message} = await updateAnalytic(newData)
        setShowToast({ 
            show: true, 
            message, 
            type: success ? 'success' : 'danger' 
        })
		resetNewData()
	}

    const resetNewData = () => {
		setNewData(nowAna)
		setShowUpdateModal(false)
	}


    return (
        <Modal  
            size="lg" 
            show={showUpdateModal} 
            onHide={resetNewData} 
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
				<Modal.Title>
                    To Ti Khon Admin mi
                </Modal.Title>
			</Modal.Header>

            <Form onSubmit={onSubmit}>
                <Modal.Body>
					<Form.Group className='m-3'>
                        <Form.Select 
                            aria-label="Default"
                            name='position'
                            required
                            value={position}
                            onChange={onChangeSelect}
                        >
                            <option value=""></option>
                            <option value='vinh'>Vinh</option>
                            <option value='cualo'>Ca L</option>
                            <option value='hoangmai'>Hong Mai</option>
                            <option value='thaihoa'>Thi Ha</option>
                            <option value='anhson'>Anh Sn</option>
                            <option value='concuong'>Con Cung</option>
                            <option value='dienchau'>Din Chu</option>
                            <option value='doluong'> Lng</option>
                            <option value='hungnguyen'>Hng Nguyn</option>
                            <option value='kyson'>K Sn</option>
                            <option value='namdan'>Nam n</option>
                            <option value='nghiloc'>Nghi Lc</option>
                            <option value='nghiadan'>Ngha n</option>
                            <option value='quephong'>Qu Phong</option>
                            <option value='quychau'>Qu Chu</option>
                            <option value='quyhop'>Qu Hp</option>
                            <option value='quynhluu'>Qunh Lu</option>
                            <option value='tanky'>Tn K</option>
                            <option value='thanhchuong'>Thanh Chng</option>
                            <option value='tuongduong'>Tng Dng</option>
                            <option value='yenthanh'>Yn Thnh</option>
                            <option value='nghean'>Ngh An (ton tnh)</option>
                            <option value='vietnam'>Vit Nam (ton nc)</option>
                            <option value='thegioi'>Th Gii (ton th gii)</option>
                        </Form.Select>
					</Form.Group>
                    
                    <Form.Group className='m-3'>
                        <Form.Label>
                            Tng s ca ghi nhn (tt c)
                        </Form.Label>
                        <Form.Control
                            type='number'
                            min='0'
                            name='total'
                            required
                            value={total}
                            onChange={onChangeDataForm}
                        />
					</Form.Group>
                    
                    <Form.Group className='m-3'>
                        <Form.Label>
                            S ca t vong (tt c)
                        </Form.Label>
                        <Form.Control
                            type='number'
                            min='0'
                            name='death'
                            required
                            value={death}
                            onChange={onChangeDataForm}
                        />
					</Form.Group>
                    
                    <Form.Group className='m-3'>
                        <Form.Label>
                            S ca cha khi/chuyn tuyn trn (ch p dng vi NA, VN)
                        </Form.Label>
                        <Form.Control
                            type='number'
                            min='0'
                            name='cured'
                            required
                            value={cured}
                            onChange={onChangeDataForm}
                        />
					</Form.Group>
                    
                    <Form.Group className='m-3'>
                        <Form.Label>
                        S ca ang iu tr (ch p dng vi NA, VN)
                        </Form.Label>
                        <Form.Control
                            type='number'
                            min='0'
                            name='cure'
                            required
                            value={cure}
                            onChange={onChangeDataForm}
                        />
					</Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant='secondary' onClick={resetNewData}>
						Cancel
					</Button>
					<Button variant='primary' type='submit'>
						Cp nht
					</Button>
                </Modal.Footer>   
            </Form>
        </Modal>
    )
}

export default UpdateAnalyticsModal

